.history {
    
    .container {
        width: 100%;
        padding: 8px 16px 0px 16px;
        flex-grow: 1;
    }

    .bg {
        background-color: #5c5c5c;
    }
    
    .title {
        display: flex;
        padding: 16px;
        align-items: center;
        font-size: 1.5rem;
        font-family: Quicksand;
        font-weight: 400;
        line-height: 1.334;
        color: #ffffff;
        display: block;
    }

    .table {
        width: 100%;
        display: table;
        border-spacing: 0;
        border-collapse: separate;

        tr, .tr {
            color: inherit;
            display: table-row;
            outline: 0;
            vertical-align: middle;
        }

        .trShow {
            background-color: #ffffff40;
        }

        th, .td {
            top: 0;
            left: 0;
            z-index: 2;
            position: sticky;
            text-align: center;
            color: #ffffff;
            font-weight: 500;
            line-height: 1.5rem;
            display: table-cell;
            padding: 10px 10px;
            font-size: 0.875rem;
            font-weight: 400;
            line-height: 1.43;
            border-bottom: 1px solid rgba(224, 224, 224, 1);
        }

        .icon {
            color:rgb(192, 93, 79);
            margin-left: 3px;
            cursor: pointer;
        }
    }
    
    .empty {
        padding: 16px;
        color: #fff;
        font-size: 1.2rem;
    }
}


.farms {
    .container {
        width: 100%;
        padding: 8px 16px 0px 16px;
        flex-grow: 1;

        .box {
            margin-bottom: 20px;
            background: radial-gradient(174.47% 188.91% at 1.84% 0%, rgb(192, 93, 79) 0%, blue 100%), rgb(255, 255, 255);

            .table {
                width: 100%;
                display: table;
                border-spacing: 0;
                border-collapse: separate;

                tr, .tr {
                    color: inherit;
                    display: table-row;
                    outline: 0;
                    vertical-align: middle;
                }

                .trShow {
                    background-color: #ffffff40;
                }

                th, .td {
                    top: 0;
                    left: 0;
                    z-index: 2;
                    position: sticky;
                    text-align: center;
                    color: #ffffff;
                    font-weight: 500;
                    line-height: 1.5rem;
                    display: table-cell;
                    padding: 10px 10px;
                    font-size: 0.875rem;
                    font-weight: 400;
                    line-height: 1.43;
                    border-bottom: 1px solid rgba(224, 224, 224, 1);
                }
            }

            .op-box {
                display: flex;
                width: 100%;
                min-height: 0px;
                height: auto;
                transition-duration: 307ms;
                transition: height 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

                .op-box-shadow {
                    box-shadow: 0px 2px 1px -1px #00000033, 0px 1px 1px 0px #00000024, 0px 1px 3px 0px #0000001f;
                }
                
                .op-box-cell {
                    flex:1;
                    min-width: 320px;
                    padding: 10px 20px;
                    border-radius: 0px;
                    height: 100%;
                    box-sizing: border-box;
                    color: #fff;
                    text-align: left;
                    .title {
                        padding: 5px;
                        font-size: 17px;
                    }
                    .subtitleBox {
                        display: flex;
                        .subtitle {
                            font-size: 0.875rem;
                            padding: 5px;
                            flex: 1;
                            text-align: left;
                        }
                    }
                    .btn {
                        color: rgba(0, 0, 0, 0.87);
                        box-shadow: none;
                        background-color: #E7C5C2;
                        margin-top: 5px;
                        border: none;
                        width: 100%;
                        padding: 6px 16px;
                        font-size: 0.875rem;
                        min-width: 64px;
                        box-sizing: border-box;
                        border-radius: 3px;
                        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
                        &.disable {
                            background-color: #d4b6b4;
                            color: rgba(0, 0, 0, 0.26);
                        }
                    }
                    .link {
                        text-decoration: underline;
                        color: lightblue;
                        cursor: pointer;
                        float: right;
                    }
                    .warp{
                        width: 100%;
                        position: relative;
                        display: flex;
                        .left {
                            flex: 1;
                            width: 100%;
                        }
                        .right {
                            width: 60px;
                            float: right;
                            margin-left: 10px;
                            align-items: center;
                            .icon {
                                font-size: 1.5rem;
                                margin-top: 8px;
                            }
                        }
                    }
                }
            }
        }
    }
    
    .title {
        display: flex;
        padding: 16px;
        align-items: center;
        font-size: 1.5rem;
        font-family: Quicksand;
        font-weight: 400;
        line-height: 1.334;
        color: #ffffff;
        display: block;
    }
    
    .empty {
        padding: 16px;
        color: #fff;
        font-size: 1.2rem;
    }
}


.xbumper {
    .container {
        width: 100%;
        padding: 8px 16px 0px 16px;
        flex-grow: 1;

        .title {
            display: flex;
            padding: 16px;
            align-items: center;
            font-size: 1.5rem;
            font-family: Quicksand;
            font-weight: 400;
            line-height: 1.334;
            color: #ffffff;
            display: block;
        }

        .box {
            margin-bottom: 20px;
            padding-bottom: 20px;
            background: radial-gradient(174.47% 188.91% at 1.84% 0%, rgb(192, 93, 79) 0%, blue 100%), rgb(255, 255, 255);

            .navigator{
                max-width: 330px;
                margin-left: auto;
                margin-right: auto;
          
                .navtab {
                  display: inline-block;
                  text-align: center;
                  width: 160px;
                  border-radius: 0px;
                  color: #ffffff80;
                  margin-right: 4px;
                  padding: 12px 8px;
                  border-top-left-radius: 5px;
                  border-top-right-radius: 5px;
                  cursor: pointer;

                  &:hover {
                      color: #fff;
                    background-color: #ffffff60;
                  }
              
                  &.active {
                    color: #fff;
                    border-bottom: 2px solid #fff;
                  }
                }
              }
            .text {
                color: #fff;
                font-size: 17px;
                text-align: center;
                padding-top: 2px;
            }
            .text2 {
                text-align: center;
                padding-top: 1px;
                line-height: 1.2rem;
                font-size: 0.95rem;
                color: #fff;
                &.future {
                    color: #bfc2ce;
                }
            }

            .balance {
                border: #fff solid 1px;
                width: 80%;
                margin-left: 10%;
                padding: 15px;
                color: #fff;
                border-radius: 4px;
            }

            .op {
                width: 80%;
                margin-left: 10%;

                .left {
                    flex: 1;
                    width: 100%;
                }
                
                .btn {
                    float: right;
                    margin-left: 10px;
                    margin-top: 12px;
                    background-color: rgba(106, 106, 106, 0.8);
                    border: none;
                    color: rgb(255, 255, 255);
                    text-transform: none;
                    height: 53px;
                    font-size: 17px;
                    width: 255px;
                }


            }
            
        }
    }
 
}

